<template>
  <v-card>
    <v-card-title>Login</v-card-title>
    <v-card-text>
      <v-alert v-if="error" type="error">{{ error }}</v-alert>
      <v-form v-model="valid" @submit.prevent="submit">
        <v-text-field
          v-model="username"
          :rules="usernameRules"
          label="Username"
        />
        <v-text-field
          v-model="password"
          :rules="passwordRules"
          label="Password"
          type="password"
        />
        <v-btn :disabled="!valid" color="primary" type="submit">Submit</v-btn>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
import GenesisService from "@/GenesisService";
import axios from "axios";

export default {
  name: "Login",
  data() {
    return {
      valid: false,
      username: "",
      usernameRules: [ v => !!v || "Username is required", v => /.+@.+\..+/.test(v) || "Username must be valid" ],
      password: "",
      passwordRules: [ v => !!v || "Password is required" ],
      error: ""
    }
  },
  methods: {
    async submit() {
      try {
        const token = await GenesisService.login({ username: this.username, password: this.password });
        axios.defaults.headers.common["Authorization"] = token;
        this.$store.commit("setToken", token);
        await this.$router.push("/gradebook");
      } catch (error) {
        this.error = error.response.data;
      }
    }
  }
}
</script>

